<template lang="pug">
    el-col
        //- 操作
        el-row.operates(v-if="operates && operates.length>0")
            el-button(v-for="(op, i) in operates" type="info" @click="operate(op.fun)" size="small" :key="op.str") {{op.str}}

        //- 表格
        el-table(:data="tableData" @current-change="chooseRow" highlight-current-row)
            el-table-column(type="index" width="50")
            template(v-for="(item, i) in keys")
                //- 图片处理
                el-table-column(:prop="item.key" :label="item.str" v-if="item.type == 'img'")
                    template(slot-scope="scope")
                        img(:src="scope.row[item.key]" alt="" style="max-width:40px;max-height:40px;")

                //- 时间戳处理
                el-table-column(:prop="item.key" :label="item.str"  v-else-if="item.type == 'time'")
                    template(slot-scope="scope")
                        span {{scope.row[item.key] | time}}

                //- 字符太长，部分省略处理
                el-table-column(:prop="item.key" :label="item.str"  v-else-if="item.type == 'els'")
                    template(slot-scope="scope")
                        span {{scope.row[item.key] | short}}

                //- 无需处理内容展示
                el-table-column(:prop="item.key" :label="item.str" v-else="")

            //- 操作
            //- el-table-column(label="操作" v-if="ops && ops.length>0 && !isCanOp" ref="operate")
                template(scope="scope")
                    template(v-for="(op, j) in ops")
                        //- 编辑
                        template(v-if="tabOp.indexOf('edit')<0")
                            el-button(type="info" @click="edit(scope)" size="small" v-if="op.f=='edit' && !condition(scope.row)" ref="edit") {{op.str}}
                            //- 编辑(冻结时的不可点击的冻结)
                            el-button(type="info" @click="edit(scope)" size="small" v-if="op.f=='edit' && condition(scope.row)" ref="edit" disabled) {{op.str}}
                        
                        //- 删除
                        template(v-if="tabOp.indexOf('del')<0")
                            el-button(type="danger" @click="shelf(scope, 1)" size="small" v-if="op.f=='del'" ref="del" id="del") {{op.str}}

                        //- 页面独特的按钮事件
                        template(v-if="tabOp.indexOf(op.ref)<0")
                            el-button(@click="self(scope, op.fun)" size="small" v-if="op.f=='self'" :type="op.type"
                            :ref="op.ref ? op.ref : ''") {{op.str}}
                        
                        //- template(v-if="tabOp.indexOf(op.ref)<0")
                            el-button(@click="self(scope, op.fun)" size="small" v-if="op.f=='self'" style="background-color:#50bfff;color:#fff;"
                            :ref="op.ref ? op.ref : ''") {{op.str}}

        el-pagination(layout="total, prev, pager, next, jumper" :total="total" :page-size="10" :current-page="currentPage" 
             @current-change="handleCurrentChange" ref="page")

</template>

<script>
var addZero = function(v) {
    return (v+'').length == 1 ? '0'+v : v;
}
export default {
    name: 'com-table',
    props: ['keys', 'operates', 'selfApi', 'tableData', 'total', 'currentPage', 'tabOp', 'canOp'],
    data(){
        return{
            isCanOp: this.canOp
        }
    },
    mounted(){},
    methods: {
        operate(fun){    // 操作
            this.$emit(fun)
        },
        handleCurrentChange(v){ // 页码更改
            this.$emit('changePage', v)
        },
        chooseRow(curItem, oldItem){
            this.$emit('chooseRow', curItem, oldItem)
        }
    },
    watch: {
        tabOp(v){
        //- console.log(this.$refs.del)
            //- v.forEach((el)=>{
            //-     this.$refs[el].$el.style.display = 'none';
            //- })
        }
    }

}
</script>

<style lang="sass" scoped>
.operates
    text-align: right;

.el-button
    margin-right: 10px;
    margin-bottom: 10px;
</style>


